<template>
  <div style="padding:20px;">
    <div style="margin-bottom:15px;display: flex;justify-content:space-between;">
      <div>
        <Button type="primary" icon="ios-plus-empty" @click="handleStoreAdd">
          添加店铺
        </Button>
      </div>
      <div>
        <div class="select-wrapper">
          <Input v-model="page.keywords" placeholder="请输入店铺名"></Input>
        </div>
        <div class="select-wrapper">
          <Input v-model="page.telphone" placeholder="请输入手机号"></Input>
        </div>
        <div class="select-wrapper">
          <Button type="ghost" class='fl-btn' @click='getData'>查询</Button>
        </div>
      </div>
    </div>
    <Table :loading="loading" border :columns="columns4" :data="tableData"></Table>
    <div style="margin-top: 15px;overflow: hidden">
      <div style="float: left;"></div>
      <div style="float: right;">
        <Page :total="totalcount" :page-size="page.pagesize" :page-size-opts="[10,20,30,40]" show-elevator show-sizer
          placement="top" show-total @on-change='indexChange' @on-page-size-change='sizeChange'></Page>
      </div>
    </div>
    <Modal v-model="showConfirm" fullscreen title="删除确认" @on-ok="updateConfirm">
      <div>确定删除该店铺吗?</div>
    </Modal>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        loading: false,
        page:{
          pageindex: 0,
          pagesize: 10,
          keywords:'',
          telphone:'',
        },
        totalcount: 0,
        columns4: [
          {
            title: '#',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return params.index + this.page.pageindex * this.page.pagesize + 1;
            }
          },
          {
            title: '店铺图片',
            width:100,
            align: 'center',
            render: (h, params) => {
              if(params.row.picture){
                return  h('img', {
                  attrs: {
                    src: params.row.picture,
                    style: 'width: 70px;height: 70px;border-radius: 5px;vertical-align: middle;margin:5px 0;'
                  }
                })
              }else{
                return  h('div', {
                  attrs: {
                    style: 'width: 70px;height: 70px;border:1px solid #666;margin:5px 0;vertical-align: middle;display:inline-block'
                  }
                })
              }
            }
          },
          {
            title: '店铺名称',
            minWidth:150,
            key: 'name',
            align: 'center',
          },
          {
            title: '店铺账号',
            minWidth:150,
            key: 'managername',
            align: 'center',
          },
          {
            title: '管理员手机号',
            width:150,
            key: 'telphone',
            align: 'center',
          },
          {
            title: '店铺邀请码',
            width:150,
            key: 'incode',
            align: 'center',
          },
          {
            title: '创建日期',
            key: 'createtime',
            width: 180,
            align: 'center',
          },
          {
            title: '操作',
            key: 'action',
            width: 180,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                  },
                  style:{
                    marginRight:'5px'
                  },
                  on: {
                    click: () => {
                      this.$router.push({ path: '/store/detail', query: { id: params.row.id } });
                    }
                  }
                }, '详情'),
                h('Button', {
                  props: {
                    type: 'warning',
                  },
                  on: {
                    click: () => {
                      this.updateForm.storeid=params.row.id;
                      this.showConfirm=true;
                    }
                  }
                }, '删除')
              ]);
            }
          }
        ],
        tableData: [],
        showConfirm:false,
        updateForm:{
          storeid:'',
          ishidden:1
        }
      }
    },
    mounted: function () {
      this.$sitemap.prvname = "店铺管理";
      this.$sitemap.currname = '店铺列表';
      this.$menuactive.open = ['StoreBlock'];
      this.$menuactive.active = 'StoreApply';
      this.$Loading.start();
      this.getData().then(() => {
        this.$Loading.finish();
      });
    },
    methods: {
      //添加店铺
      handleStoreAdd(){
        this.$router.push('/store/add');
      },
      //得到用户列表
      getData() {
        this.loading = true;
        return this.$http.post(this.$api.store_list, this.page).then(res => {
          if (res.data.result == '1' && res.data.data) {
            this.tableData = res.data.data;
            this.totalcount = +res.data.totalcount
          } else {
            this.$Message.error(res.data.msg);
          }
        }).catch(erro => {
          this.$Message.error('服务器异常');
        }).then(() => {
          this.loading = false;
        })
      },
      indexChange(index) {
        this.page.pageindex = index - 1;
        this.getData();
      },
      sizeChange(size) {
        this.page.pagesize = size;
        this.getData();
      },
      updateConfirm(){
        this.$http.post(this.$api.store_update, this.updateForm).then(res => {
          if (res.data.result == '1') {
            this.$Message.success('删除成功');
            this.getData();
          } else {
            this.$Message.error(res.data.msg);
          }
        }).catch(erro => {
          this.$Message.error('服务器异常');
        });
      }
    },
  }
</script>
<style scoped>
  .select-wrapper {
    display: inline-block;
    margin-right: 5px;
  }
</style>



